@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url('https://cdn.wallpaperhub.app/cloudcache/1/b/5/8/e/f/1b58ef6e3d36a42e01992accf5c52d6eea244353.jpg');
  background-position: center;
  font: 'Roboto', sans-serif;
}

.App {
  text-align: center;
  display: flex;
  margin-top: 250px;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.screen_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.main_screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 400px;
    width: 400px;
    background-image: url('https://mir-s3-cdn-cf.behance.net/project_modules/disp/496ecb14589707.562865d064f9e.png');
    border-radius: 3px;
    border: 2px solid rgb(236, 129, 227);
}

.counter {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 35px;
    margin: 10px;   
    background-color: rgba(205, 205, 214, 0.507);
    height: 25%;
    width: 70%;
    border: 2px solidrgb(236, 129, 227);
    border-radius: 5px;
    text-shadow: rgba(98, 0, 255, 0.719) 1px 0 10px;

}

.counter_end {
    font-size: 50px;
    color: rgb(194, 9, 9);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(137, 98, 173);
    margin: 10px;
    height: 25%;
    width: 70%;
    border: 2px solid black;
    border-radius: 5px;
    text-shadow: rgba(0, 0, 0, 0.219) 3px 3px 3px;

}

.buttons {
    display: inline-block;
}


.buttons {
    display: flex;
    justify-content: center;
    margin: 15px;
    padding: 10px;
    border-radius: 10px;
}

.button {
    background: none;
    border: 2px solid;
    border-radius: 15px;
    color: #1a082c;
    font: inherit;
    line-height: 1.3;
    margin: .5em;
    margin-top: .1em;
    padding: .7em 1em;
    transition: 0.25s;
}

.button:hover {
    box-shadow: 0 0 10px 2px rgba(196, 200, 226, 0.7);
    color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
}

.button:disabled {
    border-color:rgb(238, 140, 140);
    background-color: #945acec5;
    color: rgba(226, 126, 126, 0.808);
}

.button:active {
    background: #5e10c4cc;
    box-shadow: inset 0 0 10px 2px rgb(158, 148, 173);
    outline-style: none;
    color: black;
}
.input_container {
	color:#fff;	
}

.input {
    font-size: 15px;
    background: transparent;
    box-sizing: border-box;
    outline-style: none;
    transition: box-shadow .7s;
}

.good_value_style label {
    position:absolute;
	top:0px;
	left:0px;
	font-size:16px;
	color:#fff;	
	transition: all 0.5s ease-in-out;
}

.input:hover {
    outline-style: none;
    box-shadow: 5px 5px 3px rgba(110, 20, 212, 0.637);
}

.input:focus {
    border: none;
    outline-style: none;
}

.bad_value_style {
    background-color: rgba(230, 13, 13, 0.767);
    transition: 0.3s
}
.settings_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.settings {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 400px;
    width: 400px;
    background-image: url('https://mir-s3-cdn-cf.behance.net/project_modules/disp/496ecb14589707.562865d064f9e.png');
    border-radius: 3px;
    border: 2px solid rgb(236, 129, 227);
}
